import axios from "ts-axios-new";

const urlMap = {
  development: "/",
  production: "https://konglingwen94.github.io/vue-sell/dist/"
};
const baseUrl = urlMap[process.env.NODE_ENV];
const ERR_OK = 0;

export function get(url) {
  return function(params = {}) {
    console.log(baseUrl);

    return axios
      .get(baseUrl + url, {
        params
      })
      .then(res => {
        const { errno, data } = res.data;
        if (errno === ERR_OK) {
          return data;
        }
      })
      .catch(e => {});
  };
}
